import React, { useCallback, useEffect, useState } from "react";
import styles from "./index.module.scss";
import { useParams } from "react-router-dom";
import { BgArchive } from "@/request/api/blog/archive/index.d";
import { queryArchive } from "@/request/api/blog/archive";
import { formatDateTime } from "@/utils/dateTimeUtils";
import { Skeleton } from "antd";

export const Archives = () => {
  const { uuid } = useParams();
  console.debug("请求档案UUID:", uuid);

  const [detail, setDetail] = useState<BgArchive | null>(null);

  const queryDetail = useCallback(() => {
    queryArchive(uuid as string).then((res) => {
      console.debug("查询档案结果", res);
      if (res.data) {
        setDetail(res.data);
      }
    });
  }, [uuid]);

  useEffect(() => {
    queryDetail();
  }, [queryDetail]);

  return (
    <div className={styles.index}>
      {detail && (
        <div className="archive-content">
          <div className="title">{detail.title}</div>
          <div className="time">
            {formatDateTime(Number(detail.createDate))}
          </div>
          <div
            className="content"
            dangerouslySetInnerHTML={{ __html: detail.content }}
          ></div>
        </div>
      )}
      {!detail && <Skeleton active paragraph={{ rows: 22 }} />}
    </div>
  );
};

export default Archives;
