import { createHashRouter } from "react-router-dom";
import Root from "@/views/root";
import ErrorPage from "@/views/common/errorPage";
import Archives from "@/views/archives";
import ArchivesList from "@/views/archivesList";

export const URL_PATH_ROOT = "/";
export const URL_ARCHIVES_LIST_PATH_KEY = "/archives/list";
export const URL_ARCHIVES_LIST_PATH = `${URL_ARCHIVES_LIST_PATH_KEY}*`;
export const URL_ARCHIVES_ITEM_PATH = "/archives/:uuid";

const router = createHashRouter([
  {
    path: URL_PATH_ROOT,
    element: <Root />,
    errorElement: <ErrorPage />,
    children: [
      {
        path: URL_ARCHIVES_LIST_PATH,
        element: <ArchivesList />,
      },
      {
        path: URL_ARCHIVES_ITEM_PATH,
        element: <Archives />,
      },
    ],
  },
]);

export default router;
