import React from "react";
import styles from "./index.module.scss";
import { useSelector } from "react-redux";
import { IRootState } from "@/type";
import { useLocation, useNavigate } from "react-router-dom";
import { URL_ARCHIVES_LIST_PATH, URL_ARCHIVES_LIST_PATH_KEY } from "@/router";

export const Head = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const { headTitle } = useSelector((state: IRootState) => state.bgConfig);

  const hasCurrentMenu = (path: string) => {
    return location.pathname.includes(path);
  };

  return (
    <div className={styles.index}>
      <div className="head-content">
        <div className="head-icon-title">
          <div className="head-icon">
            <button
              className="wt-text-button"
              onClick={() => {
                navigate(URL_ARCHIVES_LIST_PATH.replace("*", ""));
              }}
            >
              <img src="/favicon.ico" alt="WT629"></img>
            </button>
          </div>
          <div className="head-title-content">
            <div className="head-title">WT629.COM</div>
            <div className="head-welcome">{headTitle}</div>
          </div>
        </div>
        <div className="head-menu">
          <button
            className={
              hasCurrentMenu(URL_ARCHIVES_LIST_PATH_KEY)
                ? "wt-text-button active"
                : "wt-text-button"
            }
            onClick={() => {
              navigate(URL_ARCHIVES_LIST_PATH.replace("*", ""));
            }}
          >
            文章列表
          </button>
        </div>
      </div>
    </div>
  );
};

export default Head;
