import React, { useCallback, useEffect, useState } from "react";
import styles from "./index.module.scss";
import {
  queryArchiveList,
  queryArchiveTypeList,
} from "@/request/api/blog/archive";
import { BgArchive, BgArchiveType } from "@/request/api/blog/archive/index.d";
import { useNavigate, useParams } from "react-router-dom";
import { fromNow } from "@/utils/dateTimeUtils";
import { URL_ARCHIVES_ITEM_PATH, URL_ARCHIVES_LIST_PATH } from "@/router";
import { Empty, Skeleton } from "antd";

export const ArchivesList = () => {
  const navigate = useNavigate();
  console.debug("params", useParams());
  const { "*": uuid } = useParams();
  const [list, setList] = useState<BgArchive[]>([]);
  const [filterList, setFilterList] = useState<BgArchive[]>([]);
  const [typeList, setTypeList] = useState<BgArchiveType[]>([]);

  const getTypeCount = (uuid: string) => {
    let result = 0;
    if (list?.length > 0) {
      result = list.filter(
        (archive) => archive.bgArchivesType.uuid === uuid
      ).length;
    }
    return result;
  };

  const query = useCallback(() => {
    queryArchiveList().then((res) => {
      console.debug("查询档案列表结果", res);
      if (Array.isArray(res.data)) {
        const compareFn = (i1: BgArchive, i2: BgArchive) =>
          Number(i2.createDate) - Number(i1.createDate);
        const resultList = [...res.data].sort(compareFn);
        setList(resultList);
        if (!uuid) {
          setFilterList(resultList);
        } else {
          setFilterList(
            resultList.filter((archive) => archive.bgArchivesType.uuid === uuid)
          );
        }
      }
    });
    queryArchiveTypeList().then((res) => {
      console.debug("查询档案类型列表结果", res);
      if (Array.isArray(res.data)) {
        setTypeList(res.data);
      }
    });
  }, [uuid]);

  useEffect(() => {
    query();
  }, [query, uuid]);

  return (
    <div className={styles.index}>
      <div className="archive-type-list">
        {typeList?.length > 0 &&
          typeList.map((type) => (
            <button
              className={
                type.uuid === uuid
                  ? "type-item wt-text-button active-button"
                  : "type-item wt-text-button"
              }
              key={type.uuid}
              onClick={() => {
                navigate(
                  `${URL_ARCHIVES_LIST_PATH.replace("*", `/${type.uuid}`)}`
                );
              }}
            >
              <div className="type-title">{type.name}</div>
              <div className="type-count">{getTypeCount(type.uuid)}</div>
            </button>
          ))}
      </div>
      <div className="archive-list">
        {list?.length > 0 &&
          filterList?.length > 0 &&
          filterList.map((archive) => {
            return (
              <div className="archive-item" key={archive.uuid}>
                <div className="type">
                  <div className="type-content">
                    {archive.bgArchivesType.name}
                  </div>
                </div>
                <div className="title">
                  <button
                    className="wt-text-button"
                    onClick={() => {
                      navigate(
                        `${URL_ARCHIVES_ITEM_PATH.replace(
                          ":uuid",
                          archive.uuid
                        )}`
                      );
                    }}
                  >
                    {archive.title}
                  </button>
                </div>
                <div className="time">
                  {fromNow(Number(archive.createDate))}
                </div>
              </div>
            );
          })}
        {list?.length > 0 && filterList?.length === 0 && (
          <Empty description="该分类下暂无内容" />
        )}
        {!list && <Skeleton active paragraph={{ rows: 22 }} />}
      </div>
    </div>
  );
};

export default ArchivesList;
