import React, { useCallback, useEffect } from "react";
import "./index.scss";
import styles from "./index.module.scss";
import Head from "./head";
import Foot from "./foot";
import { Outlet, useLocation, useNavigate } from "react-router-dom";
import { queryBgConfig } from "@/request/api/blog/bgConfig";

import { useSelector, useDispatch } from "react-redux";
import { setState } from "@/store/slice/bgConfigSlice";
import { IRootState } from "@/type";
import { URL_ARCHIVES_LIST_PATH, URL_PATH_ROOT } from "@/router";

export const Index = () => {
  const navigate = useNavigate();
  const location = useLocation();
  const dispatch = useDispatch();
  const { pageTitle } = useSelector((state: IRootState) => state.bgConfig);

  const init = useCallback(() => {
    queryBgConfig().then((res) => {
      if (res.data) {
        dispatch(setState(res.data));
      }
    });
    console.debug("当前页面", location);
    if (location.pathname === URL_PATH_ROOT) navigate(URL_ARCHIVES_LIST_PATH);
  }, [dispatch, location, navigate]);

  useEffect(() => {
    document.title = pageTitle;
  }, [pageTitle]);

  useEffect(() => {
    console.debug("index页面加载");
    init();
  }, [init]);

  return (
    <div className={styles.index}>
      <Head />
      <div className="page-content">
        <Outlet />
      </div>
      <Foot />
    </div>
  );
};

export default Index;
